 .obtacle{
   outline: 1px solid #2C3E50;
   background-color: #2C3E50;
   -webkit-animation: wallAnimation 250ms ease-in;
           animation: wallAnimation 250ms ease-in;
}
/* -------- animation ------ */

@-webkit-keyframes wallAnimation {
   0%{
      -webkit-transform: scale(1);
              transform: scale(1);
   }
   50%{
      -webkit-transform: scale(1.15);
              transform: scale(1.15);
   }
   100%{
      -webkit-transform: scale(1);
              transform: scale(1);
   }
}

@keyframes wallAnimation {
   0%{
      -webkit-transform: scale(1);
              transform: scale(1);
   }
   50%{
      -webkit-transform: scale(1.15);
              transform: scale(1.15);
   }
   100%{
      -webkit-transform: scale(1);
              transform: scale(1);
   }
}

.node-visited{
   width: 30px;
   height: 30px;
   outline: 1px solid rgb(107, 218, 233);
   -webkit-animation-name: visitedAnimation;
           animation-name: visitedAnimation;
   -webkit-animation-duration: 1.5s;
           animation-duration: 1.5s;
   -webkit-animation-iteration-count: 1;
           animation-iteration-count: 1;
   -webkit-animation-fill-mode: forwards;
           animation-fill-mode: forwards;
   -webkit-animation-play-state: running;
           animation-play-state: running;
}


@-webkit-keyframes visitedAnimation {
   0%{
      -webkit-transform: scale(0.3);
              transform: scale(0.3);
      background-color: rgba(0, 0, 66, 0.75);
      border-radius: 50%;
   }
   50%{
      background-color: rgba(17, 104, 217, 0.75);
   }
   75%{
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      background-color: rgba(0, 217, 159, 0.75);
   }
   100%{
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgba(0, 190, 218, 0.75);
   }
}


@keyframes visitedAnimation {
   0%{
      -webkit-transform: scale(0.3);
              transform: scale(0.3);
      background-color: rgba(0, 0, 66, 0.75);
      border-radius: 50%;
   }
   50%{
      background-color: rgba(17, 104, 217, 0.75);
   }
   75%{
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      background-color: rgba(0, 217, 159, 0.75);
   }
   100%{
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgba(0, 190, 218, 0.75);
   }
}

.shortestPath{
   width: 30px;
   height: 30px;
   outline: 1px solid #FFC300;
   -webkit-animation-name: shortestPathAnimation;
           animation-name: shortestPathAnimation;
   -webkit-animation-duration: 500ms;
           animation-duration: 500ms;
   -webkit-animation-iteration-count: 1;
           animation-iteration-count: 1;
   -webkit-animation-fill-mode: forwards;
           animation-fill-mode: forwards;
   -webkit-animation-play-state: running;
           animation-play-state: running;
}

@-webkit-keyframes shortestPathAnimation {
   0%{
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
      background-color: rgba(151, 143, 70, 0.75);
      border-radius: 50%;
   }
   50%{
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
      background-color: rgba(192, 187, 39, 0.75);
   }
   75%{
      -webkit-transform: scale(0.9);
              transform: scale(0.9);
      background-color: rgba(247, 235, 130, 0.75);
   }
   100%{
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: #FFC300;
   }
}

@keyframes shortestPathAnimation {
   0%{
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
      background-color: rgba(151, 143, 70, 0.75);
      border-radius: 50%;
   }
